import { define, WeElement, classNames } from 'omi'




const  rabbit = 'https://act.weixin.qq.com/static/images/201909/194d229378cf42d9d890cc839bcecab1.png'
const  hat= 'https://act.weixin.qq.com/static/images/201909/9d21b94a6464e25f76744519c8938cc1.png'
const btn ='https://act.weixin.qq.com/static/images/201909/1305ee9562df081609dbdfdfb57a625d.png'
const wording = 'https://act.weixin.qq.com/static/images/201909/3ad910782354751681f09c641b747a0d.png'

const logoWord = 'https://act.weixin.qq.com/static/images/201909/e5560afb69bcee9088fc96ca5d34939f.png'
const logoW = 'https://act.weixin.qq.com/static/images/201909/78350a478f7b85ac5b8f865b1df0a52f.png'
const logo = 'https://act.weixin.qq.com/static/images/201909/b1ccb3d3471a3d4186a044a3476e2cdb.png'


define('my-app', class extends WeElement {
  static css = require('./_index.less')
  playbgm = true
  arr = ['https://act.weixin.qq.com/static/images/201909/e94d20ff9829a1c10fdb6e7e2dae612b.png',
    'https://act.weixin.qq.com/static/images/201909/41637242bb0bee5df5b7b38673981b9b.png',
    'https://act.weixin.qq.com/static/images/201909/38c314d354c41375137cb5edd1ebea3f.png',
    'https://act.weixin.qq.com/static/images/201909/978eb61c5c5444d14a51e2b3eca4b309.png',
    'https://act.weixin.qq.com/static/images/201909/33b44808391e1cbe05839fb64b42b87c.png',
    'https://act.weixin.qq.com/static/images/201909/fde7d0831d2f11625e7e8152084410b5.png',
    'https://act.weixin.qq.com/static/images/201909/cd03f21cab5a4681287c224b1680dbd4.png',
    'https://act.weixin.qq.com/static/images/201909/9accbb76c296a68079a8a70da44ae165.png',
    'https://act.weixin.qq.com/static/images/201909/9b188fcb28b9e6c6e0f9dadb321a08ae.png',
    'https://act.weixin.qq.com/static/images/201909/6ec82e321038d5f3b921a9180273b545.png',
    'https://act.weixin.qq.com/static/images/201909/6c046ca5a08e3dba2389aad9e4049137.jpg',]

  darr = [
    'https://act.weixin.qq.com/static/images/201909/75ef71f5dc093a1406cf3c2af9473a00.png',
    'https://act.weixin.qq.com/static/images/201909/75d651153a2023a9161bc59c82529db7.png',
    'https://act.weixin.qq.com/static/images/201909/2026fae74c77715914a5c0fe9b3f5a09.png',
    'https://act.weixin.qq.com/static/images/201909/d9b8a93be3d9ef1b387cc464e77601d3.png',
    'https://act.weixin.qq.com/static/images/201909/c25245be993671e847626286e1ba44e4.png'
  ]

  open = false
  room = false
  showRabbit = false
  showCat = false
  first = true
  showEnd = false

  swBgm=()=>{
    this.playbgm = !this.playbgm
    this.update()
    if(this.playbgm){
      document.getElementById('car_audio').play()
    }else{
      document.getElementById('car_audio').pause()
    }
  }
  updated() {
    if (this.canvas && !this.ctx) {
      this.ctx = this.canvas.getContext('2d')
      this.draw(this.ctx, window.innerWidth*2 / 3, window.innerHeight*2 / 3)
    }
  }

  clickHandler = () => {
    location.href = 'https://mp.weixin.qq.com/mp/homepage?__biz=Mzg2MjA1NTg2MQ==&hid=8&sn=8e685d1a3e3c35483b719342c239bf6e&scene=18'
  }
  start = (evt) => {

    this.draw(this.ctx, evt.touches[0].pageX, evt.touches[0].pageY)
    this.checkEvt(evt)
  }

  canvasClick = (evt) => {
    this.draw(this.ctx, evt.pageX, evt.pageY)
    this.checkEvtPc(evt)
  }

  canvasMove = (evt) => {
    this.draw(this.ctx, evt.pageX, evt.pageY)
  }

  checkEvtPc(evt) {
    const x = evt.pageX
    const y = evt.pageY
    if (dis(x, y, 425 * window.innerWidth / 750 + 180 * window.innerWidth / 750 / 2, 653 * window.innerWidth / 750 + 100 * window.innerWidth / 750 / 2) < 70) {

      this.showRabbit = true
      this.update()
			this.rbA.play()
			
			setTimeout(()=>{
				this.room1 = false
				this.room2 = true
				console.log(111)
				this.update()
			},1000)
    } else {
      this.showRabbit = false
      this.update()
    }

    if (dis(x, y, 102 * window.innerWidth / 750 + 350 * window.innerWidth / 750 / 2, 902 * window.innerWidth / 750 + 180 * window.innerWidth / 750 / 2) < 70) {
      setTimeout(() => {
        this.showCat = true
        this.catA.play()
        this.update()

        setTimeout(()=>{
					this.room2 = false
					this.room3 = true
					this.update()
				},2000)
      }, 1000)

    } else {
      this.showCat = false
      this.update()
    }

    if (dis(x, y, window.innerWidth / 2, 302 * window.innerWidth / 750 + 242 * window.innerWidth / 750 / 2) < 70) {

      this.play()

    }

  }

	room1 = false

  checkEvt(evt) {
    const x = (evt.changedTouches && evt.changedTouches.length > 0) ? evt.changedTouches[0].pageX : evt.touches[0].pageX
    const y = (evt.changedTouches && evt.changedTouches.length > 0) ? evt.changedTouches[0].pageY : evt.touches[0].pageY
    if (this.room1&& dis(x, y, 425 * window.innerWidth / 750 + 180 * window.innerWidth / 750 / 2, 653 * window.innerWidth / 750 + 100 * window.innerWidth / 750 / 2) < 70) {

      this.showRabbit = true
      this.rbA.play()
			this.update()
			setTimeout(()=>{
				this.room1 = false
				this.room2 = true
				console.log(111)
				this.update()
			},3000)
    } else {
      this.showRabbit = false
      this.update()
    }

    if (this.room2&&dis(x, y, 102 * window.innerWidth / 750 + 350 * window.innerWidth / 750 / 2, 902 * window.innerWidth / 750 + 180 * window.innerWidth / 750 / 2) < 60) {
      setTimeout(() => {
        this.showCat = true
				this.catA.play()
		
				this.update()
				
				setTimeout(()=>{
					this.room2 = false
					this.room3 = true
					this.update()
				},2000)
      }, 1000)

    } else {
      this.showCat = false
      this.update()
    }

    if (this.room3&&dis(x, y, window.innerWidth / 2, 302 * window.innerWidth / 750 + 242 * window.innerWidth / 750 / 2) < 60) {

      this.play()

    }

  }
  move = (evt) => {
    evt.preventDefault()
    this.draw(this.ctx, evt.touches[0].pageX, evt.touches[0].pageY)
    this.checkEvt(evt)

  }

  end = (evt) => {
    this.checkEvt(evt)
  }

  draw(ctx, x, y) {
		if(!this.canvas) return
    ctx.clearRect(0, 0, this.canvas.width, this.canvas.height)
    ctx.save()
    ctx.fillStyle = 'rgba(0,0,0,.6)'
    ctx.fillRect(0, 0, this.canvas.width, this.canvas.height)


    ctx.beginPath();
    // 添加圆弧
    ctx.translate(x, y)
    ctx.arc(0, 0, 50, 0, Math.PI * 2, true);
    ctx.closePath();
    ctx.lineWidth = 12;
    // 再次创建圆形渐变
    const lg2 = ctx.createRadialGradient(0, 0, 5, 0, 0, 100);
    // 向圆形渐变上添加颜色
    lg2.addColorStop(0, "black");
    //rgba(0,0,0,0)
    lg2.addColorStop(1, 'black');
    // 设置使用圆形渐变作为填充颜色
    ctx.fillStyle = lg2;

    ctx.globalCompositeOperation = 'xor'
    ctx.fill();

    ctx.restore()
  }

  current = 0
  playAni = false
  showVideo = false
  play() {
		if(!this.room3) return
    if (this.playAni) return
    this.playAni = true
    this.update()
    this.tick = setInterval(() => {
      this.current++
      if (this.current < 11) {

        this.aniImg.src = this.arr[this.current]
      } else {
        this.playAni = false
        clearInterval(this.tick)

        document.getElementById('car_audio').pause()
        this.room = false
        this.showVideo = true
        
        this.update()
        this.vdEle.play()
        
        // setTimeout(() => {
        //   this.vdEle.play()
        // }, 0)

        // this.vdTick = setInterval(()=>{
        //   if(!this.checkVdPlaying(this.vdEle)){
        //     this.vdEle.play()
        //   }else{
        //     clearInterval(this.vdTick)
        //   }
        // },300)
      }

    }, 200)
    this.playAni = true

  }
  checkVdPlaying(v){
    return !!(v.currentTime > 0 && !v.paused && !v.ended && v.readyState > 2)
  }
  currentDoor = 0
  playDoorAni = false
  playDoor() {

    if (this.playDoorAni) return
    this.playDoorAni = true
    this.update()
    this.tick = setInterval(() => {
      this.currentDoor++
      if (this.currentDoor < 5) {

        this.aniDoorImg.src = this.darr[this.currentDoor]
      } else {
        this.playDoorAni = false
        clearInterval(this.tick)

        this.open = true
				this.room = true
				this.room1 = true
        this.first = false
        this.update()

      }

    }, 100)

  }

  installed() {
    this.catA = document.createElement("audio");
    this.catA.src = 'http://py214dhn4.bkt.clouddn.com/mao.mp3'
    this.doorA = document.createElement("audio");
    this.doorA.src = 'http://py214dhn4.bkt.clouddn.com/meng.mp3'
    this.rbA = document.createElement("audio");
    this.rbA.src = 'http://py214dhn4.bkt.clouddn.com/%E5%86%92%E5%87%BA.mp3'


    var loaded = false
    document.body.addEventListener('touchstart',()=>{
    
      if(!loaded){
        this.vdEle.load();
        loaded = true
      }
      
    })
  }
  vEnd = () => {
    this.showEnd = true
    this.showVideo = false
    this.update()


    document.getElementById('car_audio').play()
  }

  render() {
    return (
      <div class="app" style={{ width: window.innerWidth, height: window.innerHeight }} >
        {this.first && <div class="fp" style={{ width: window.innerWidth, height: window.innerHeight }}>
          <img class="lw" src={logoWord} width={window.innerWidth * 0.9}></img>
          <img class="logo" src={logo} width={window.innerWidth / 4} style={{ left: window.innerWidth / 14, top: 30 }}></img>
          {!this.playDoorAni && <img class='door' onClick={
            () => {

              this.playDoor()
              this.doorA.play()

            }
          } src='https://act.weixin.qq.com/static/images/201909/32be4fd5d8a7e635341ab0b1a4973bbc.png' style={{ left: window.innerWidth * 0.1, top: window.innerHeight * 0.1 }} width={window.innerWidth * 0.8} height={1334 / 750 * window.innerWidth * 0.8}></img>}

          {this.playDoorAni && <img class='door' ref={e => this.aniDoorImg = e} width={window.innerWidth} src={this.darr[this.current]} style={{ left: window.innerWidth * 0.1, top: window.innerHeight * 0.1 }} width={window.innerWidth * 0.8} height={1334 / 750 * window.innerWidth * 0.8}></img>}
          <img class="mask rotate" src='https://act.weixin.qq.com/static/images/201909/4ca2203192d278926834dc5687fc7584.png' style={{ width: window.innerWidth * 1.2, height: window.innerHeight * 1.2 }}></img>
        </div>}

        {this.room &&
          <div>
            <img class="logo" src={logo} width={window.innerWidth / 4} style={{ left: window.innerWidth / 14, top: 30 }}></img>
            <canvas onClick={this.canvasClick} onMouseMove={this.canvasMove} ref={e => this.canvas = e} ontouchstart={this.start} ontouchmove={this.move} width={window.innerWidth} height={window.innerHeight}></canvas>
            <img class="room" src="https://act.weixin.qq.com/static/images/201909/2a62b7bc152eee0cb8e95c5ca61c1783.jpg" width={window.innerWidth} ></img>
						
            <div>
						{this.room1 && 
             <span> <img src={rabbit} class={classNames('rb', {
                totop: this.showRabbit
              })} width={120 * window.innerWidth / 750} style={{ left: 455 * window.innerWidth / 750, top: 625 * window.innerWidth / 750 }} />
              <img src={hat} class='hat' width={180 * window.innerWidth / 750} style={{ left: 425 * window.innerWidth / 750, top: 653 * window.innerWidth / 750 }} /></span>
						}
              {this.showCat && this.room2 && <img src="https://act.weixin.qq.com/static/images/201909/8bc1ad00c706dabf0dd50994c9ccfcc7.png" class='cat' width={350 * window.innerWidth / 750} style={{ left: 12 * window.innerWidth / 750, top: 902 * window.innerWidth / 750 }} />}

							{!this.showCat && this.room2 && <img src="https://act.weixin.qq.com/static/images/201909/72f1c830255992daa9ce90d87acba537.png" class='cat' width={350 * window.innerWidth / 750} style={{ left: 12 * window.innerWidth / 750, top: 902 * window.innerWidth / 750 }} />}
            </div>

            {this.playAni && this.room3 && <img class='ani' ref={e => this.aniImg = e} width={window.innerWidth} src={this.arr[this.current]}></img>}


          </div>
        }

      
          <video   preload="metadata"     
            x5-video-player-type="h5" playsinline="true"
            webkit-playsinline="true" x-webkit-airplay="true" x5-video-orientation="portraint" x5-video-player-fullscreen="true"  style={{ 
              // top: (window.innerHeight - 1334 * window.innerWidth / 750) / 2 ,
              visibility: this.showVideo?'visible':'hidden',
               'min-height': window.innerHeight }} ref={e => this.vdEle = e} src="http://py214dhn4.bkt.clouddn.com/8618f12043a2395561dc32af21b66643.mp4" onended={this.vEnd} controls="controls"></video>

        


        {this.showEnd &&
          <div class='end' style={{ width: window.innerWidth, height: window.innerHeight }}>
            <img class="bge" src='https://act.weixin.qq.com/static/images/201909/5b6752d9b3765fc1077f1d133fa00320.jpg' width={window.innerWidth} height={window.innerHeight}></img>
            {/* <img class="logo" src={logo} width={window.innerWidth/4}  style={{left:window.innerWidth*1.5/4, top: 80}}></img> */}
            <img class="logoW" onClick={this.clickHandler} src="https://act.weixin.qq.com/static/images/201909/9cc0afaac09a9b0f13b0e9de56643cb6.png" width={window.innerWidth / 1} style={{ left: (window.innerWidth - window.innerWidth / 1) / 2, top: -20 }}></img>

            {/* <img onClick={this.clickHandler} src={btn} width={window.innerWidth / 2} style={{ left: (window.innerWidth - window.innerWidth / 2) / 2, top: 962 * window.innerWidth / 750 }}></img>
            <img src={wording} width={window.innerWidth / 1.2} style={{ left: 30, bottom: 30 }}></img> */}

          </div>
        }

<img onclick={this.swBgm} class={classNames({
  runAnimation: this.playbgm,
  stopAnimation: !this.playbgm,
})} src="//res.maka.im/cdn/maka/release/music_icon.png?32fb85122f139626cd4982d113498ac8" style="width: 35.1562px; height: 35.1562px; right: 17.5781px; top: 17.5781px; opacity: 1; position: absolute; display: block; z-index: 1000000; transform-origin: center center; animation: 5s linear 0s infinite normal forwards running rotateRound; touch-action: pan-y; user-select: none; -webkit-user-drag: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0);"></img>
      </div>
    )
  }
})

// 帽子
// 440
// 655


function dis(x1, y1, x2, y2) {
  var a = x1 - x2;
  var b = y1 - y2;

  return Math.sqrt(a * a + b * b);
}

